.home-r-content {
  display: flex;
  justify-content: center;
  flex-direction: column;

  /* background-color: rgb(248, 248, 248); */
  width: 100%;
}

.home-r-back {
  width: 100%;
}

.home-r-title {
  text-align: center;
  color: transparent;
  background-clip: text;
  background-image:
    linear-gradient(
      90deg,
      rgb(235 105 255) 0%,
      rgb(78 208 255) 40%,
      rgb(248 110 0 / 100%) 66.6%,
      rgb(255 26 26) 100%
    );

  /* color: rgba(0, 0, 0, 0.9); */
}

.home-r-pro-name {
  text-align: center;
  color: transparent;
  background-clip: text;
  background-image:
    linear-gradient(
      90deg,
      rgb(235 105 255) 0%,
      rgb(78 208 255) 40%,
      rgb(248 110 0 / 100%) 66.6%,
      rgb(255 26 26) 100%
    );

  /* color: rgba(0, 0, 0, 0.8); */
}

.home-r-pro-desc {
  text-align: center;
  color: transparent;
  background-clip: text;
  background-image:
    linear-gradient(
      90deg,
      rgb(235 105 255) 0%,
      rgb(78 208 255) 40%,
      rgb(248 110 0 / 100%) 66.6%,
      rgb(255 26 26) 100%
    );

  /* color: rgba(0, 0, 0, 0.9); */
}

.sample-content {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

@media screen and (max-width: 910px) {

  .home-r-content {
    height: 700px;
  }

  .home-r-title {
    padding-bottom: 8px;
    font-size: 20px;
    font-weight: 400;
    text-align: center;
  }

  .home-r-pro-name {
    padding-bottom: 8px;
    font-size: 64px;
    font-weight: 800;
  }

  .home-r-pro-desc {
    padding-top: 12px;
    padding-bottom: 32px;
    font-size: 14px;
    font-weight: 500;
  }
}

